body {
  background-color: #ffffff;
  margin: 0 auto;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 140px 140px 1fr;
  overflow-x: hidden;
}
@media (min-width: 768px) {
  body {
    display: grid;
    grid-template-columns: 1fr 132px 132px 132px 1fr;
    column-gap: 60px;
  }
}
@media (min-width: 1440px) {
  body {
    display: grid;
    grid-template-columns: 1fr 245px 245px 245px 245px 1fr;
    column-gap: 80px;
  }
}

.page-main {
  grid-column: 1/-1;
  margin: 0;
}
@media (min-width: 768px) {
  .page-main {
    grid-column: 1/-1;
  }
}
@media (min-width: 1440px) {
  .page-main {
    grid-column: 1/-1;
  }
}

@media (min-width: 1440px) {
  .page-main--index::before {
    position: absolute;
    content: "";
    width: 49.5%;
    height: 694px;
    background-color: #65a83e;
    top: 0;
    right: 0;
    z-index: -1;
    background-image: url("../img/index-background-desktop.jpg");
    background-repeat: no-repeat;
    background-position: right;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .page-main--index::before {
    background-image: url("../img/index-background-desktop@2x.jpg");
  }
}

