.navigation__toggle {
  width: 25px;
  height: 25px;
  position: absolute;
  top: -30px;
  right: 20px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}

@media (min-width: 768px) {
  .navigation__item:not(:last-child) {
    margin-right: 36px;
  }
}
