  }
}

.navigation--closed .navigation__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

.navigation--opened .navigation__toggle {
  top: -35px;
  right: 18px;
}
.navigation--opened .navigation__toggle::before, .navigation--opened .navigation__toggle::after {
  content: "";
  position: absolute;
  top: 11px;
  right: 0;
  width: 25px;
  height: 2px;
  background-color: #000000;
}
.navigation--opened .navigation__toggle::before {
  transform: rotate(46deg);
  box-shadow: none;
}
.navigation--opened .navigation__toggle::after {
  transform: rotate(-45deg);
}

@media (max-width: 767px) {
  .navigation--closed .navigation__item {
    display: none;
  }
}
.navigation--nojs .navigation__toggle {
  display: none;
}

.navigation--nojs .navigation__wrapper {
  position: static;
  min-height: 0;
}

.navigation--nojs .navigation__item {
  display: block;
}

