  .control__input[type=checkbox]:checked + .control__mark::before {
    top: 8px;
    left: 5px;
    height: 30px;
  }
}

.control__input[type=radio] + .control__mark {
  border-radius: 50%;
}

.control__input[type=radio]:checked + .control__mark::before {
  position: absolute;
  top: 8px;
  left: 8px;
  width: 13px;
  height: 13px;
  background-color: #68b738;
  border-radius: 50%;
  content: "";
}
